import React, { Component } from 'react'

const GIF_LOADING_SRC = 'data:image/gif;base64,'

class Gif extends Component {
  constructor (props) {
    super(props)
  }

  render () {
    const size = this.props.size || 24
    return (
      <div className='gif'>
        <img style={{ width: size, height: size }} src={GIF_LOADING_SRC} />
      </div>
    )
  }
}

export default Gif



